.about{
	height: 500px;
	width: 100%;
	margin-top: 12%;
	background: $gray;
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 4;

	//used to create diagonal lines
	&:before{
		content: "";
		position: absolute;
		height: 1000px;
		width: 100%;
		background: $gray;
		bottom: 0;
		left: 0;
	}

	//used to create diagonal lines
	&:after{
		content: "";
		position: absolute;
		height: 1000px;
		width: 100%;
		background: linear-gradient(0deg, $white -350px, $gray);
		bottom: -20%;
		left: 0;
		transform: skewY(-2deg);
	}

	.about-content-container{
		display: flex;
		width: 1050px;
	}

	.about-left{
		position: relative;
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		z-index: 4;

		.about-img{
			width: 300px;
			height: auto;
			border-radius: 500px;
			margin-top: 25px;
			position: relative;
			opacity: 0;
			top: -50px;
			transition: ease-out all 0.5s;
			box-shadow: 0px 2px 2px 0px gray;
		}
	}

	@media only screen and (max-width: $medium-screen){
		.about-left{
			.about-img{
				width: 250px;
			}
		}
	}

	@media only screen and (max-width: $mobile){
		height: 800px;
		.about-content-container{
			flex-direction: column;
		}

		&:after{
			height: 500px;
			bottom: -5%;
		}
	}

	@media only screen and (max-width: $mobile-small){
		margin-top: -10px;
	}
}